<template>
  <property-template
    title=" "
    description="            .
              .
              "
    :objects="objects"
  />
</template>

<script>
import PropertyTemplate from "../../components/PropertyTemplate";

export default {
  components: {
    PropertyTemplate
  },
  metaInfo: {
    title: " "
  },
  data() {
    return {
      objects: []
    };
  },
  created() {
    this.getObjects();
  },
  methods: {
    getObjects() {
      this.axios
        .get(
          "common/real-estate-featured?rec_ids[]=2&ct=2&realized=false&arc=false"
        )
        .then(res => {
          this.objects = res.data.data;
        });
    }
  }
};
</script>
